import '../SingleComicPage/singleComic.scss';
import { useParams, Link } from "react-router-dom";
import { useEffect, useState } from 'react';

import MarvelService from '../../../services/MarvelService';
import { setContent } from '../../../utils/setSomeContent';

const SingleChar = () => {

    const {getCharacterByName, process, setProcess} = MarvelService();
    const {charId} = useParams();

    const [char, setChar] = useState({});

    useEffect(() => {
        getCharacterByName(charId)
        .then(res => {
            setChar(res);
        })
        .then(() => {
            setProcess('confirmed')
        });
    },[]);

    const renderChar = () => {
        return (
            <>
                <img src={char.thumbnail} alt="x-men" className="single-comic__img"/>
                    <div className="single-comic__info">
                        <h2 className="single-comic__name">{char.name}</h2>
                        <p className="single-comic__descr">{char.description}</p>
                        <a href={char.homepage} className="single-comic__descr">link: homepage</a>
                    </div>
                <Link to="/react-app-m/" className="single-comic__back">Back to all</Link>
            </>
        )
    };
    
    return (
        <div className="single-comic">
            {setContent(process, renderChar())}
        </div>
    )
}

export default SingleChar;