import AppHeader from "../appHeader/AppHeader";
import MainPage from "../pages/MainPage";
import ComicsPage from "../pages/ComicsPage";
import SingleComic from "../pages/SingleComicPage/SingleComic";
import SingleChar from "../pages/SingleCharPage/SingleChar";
import Spinner from "../spinner/Spinner";

import { Suspense, lazy } from "react";

import {
    BrowserRouter as Router,
    Routes,
    Route
} from "react-router-dom";

const Page404 = lazy(() => import('../pages/Page404'));


function App()  {

    return (
        <Router>
            <div className="app">
                <AppHeader/>
                <main>
                    <Suspense fallback={<Spinner/>}>
                        <Routes>
                            <Route path="/react-app-m/" element={<MainPage/>}/>
                            <Route path="/react-app-m/comics" element={<ComicsPage/>}/>
                            <Route path="/react-app-m/comics/:comicId" element={<SingleComic/>}/>
                            <Route path="/react-app-m/chars/:charId" element={<SingleChar/>}/>
                            <Route path="/react-app-m/*" element={<Page404/>}/>
                        </Routes>
                    </Suspense>
                </main>
            </div>
        </Router>
    )
}

export default App;